// Initialize i18n asynchronously to avoid blocking the main thread
const initI18n = async () => {
  await i18n
    .use(LanguageDetector)
    .use(initReactI18next)
    .init({
      resources,
      fallbackLng: "en",
      supportedLngs: ["en", "zh", "pt"],
      interpolation: {
        escapeValue: false,
      },
      detection: {
        order: ["localStorage", "navigator"],
        caches: ["localStorage"],
      },
      // Enable Suspense support for async loading
      react: {
        useSuspense: false, // Disable suspense to prevent blocking
      },
    });
};

// Start initialization immediately but don't block
initI18n();

export default i18n;
